import React from 'react';
import logo from './EN.png'
function About(props) {
    return (
        <div style={{textAlign:'center'}}>
            <div>
            <img src={logo} style={{width:'50vh'}}/>
            </div>
            <p>Сайт розроблено в рамках реалізації проекту Ерасмус+ Модуль Жана Монне “Інтеграція політики та засад Єдиного Здоров'я ЄС в Україні” (101048229 — EU4OH — ERASMUS–JMO–2021–HEI–TCH–RSCH) за підтримки Європейського Союзу."</p>
            <p>The site was developed in the framework of the Erasmus+ Jean Monnet project “Integration EU One Health framework and policies in Ukraine” (101048229 — EU4OH — ERASMUS–JMO–2021–HEI–TCH–RSCH) co-funded by the European Union.</p>
        </div>
    );
}

export default About;